html{
  scroll-behavior: smooth;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   padding:0;
   height:100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

footer {
  position: relative;
  width: 100%;
  bottom: 0;
}

#container {
  min-height:100%;
  position:relative;
}

.displayProp {
  display: none;
}

.fixed {
  position: fixed;
}

.quesHeight{
  overflow: scroll;
  max-height: 500px;
  width: 75%;
    margin-left: 14%;
}

/* Popup style */
.popup-box {
  position: fixed;
  /* background: #00000050; */
  /* width: 100%; */
  /* height: 100vh; */
  top: 0;
  /* left: 0; */
  right: 0;
  z-index: 98;
}
 
.box {
  position: relative;
  width: 100%;
  margin: 0 auto;
  height: auto;
  /* max-height: 70vh; */
  margin-top: calc(100vh - 85vh - 20px);
  background: #fff;
  border-radius: 4px;
  padding: 20px;
  border: 1px solid #999;
  overflow: auto;
}
 
.close-icon {
  content: 'x';
  cursor: pointer;
  position: fixed;
  right: calc(15% - 30px);
  top: calc(100vh - 85vh - 33px);
  background: #ededed;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  line-height: 20px;
  text-align: center;
  border: 1px solid #999;
  font-size: 20px;
}

.overViewSection {
  min-height: 600px;
  border-left: 1px solid #6366f1;
  border-right: 1px solid #6366f1;
  border-bottom: 1px solid #6366f1;
  text-align: left;
}

/* course details css */
.cource-details-section{
  border-bottom: 1px solid #6366f1;
}

.cource-details-section .bg-gray-100 {
  --tw-bg-opacity: 1;
   background:none;
